@use '../base/variables' as v;
@use 'base/mixins' as m;

.line-up{
    background-color: v.$pink;
    &__title{
        color: v.$white;
    }
    &__subtitle{
        color: v.$white;
        font-size: 1.4rem;
        text-align: center;
        margin-bottom: 0;
        padding: 2rem;
        &--black{
            color: v.$black;
            font-weight: 400;
        }
        @include m.semi-tablet {
            padding: 3rem 0;
            font-size: 2rem
        }
    }
    &__card-container{
        @include m.grid (1, 2rem);
        @include m.tablet {
            @include m.grid(2, 10rem);
        }
    }
    &__card{
        padding-top: 1.5rem;
        background-color: v.$blue;
        box-shadow: 0px 2px 6px v.$black;
        &--green{
            background-color: v.$green-ligth;
        }
    }
    &__img{
        width: 30%;
        max-width: 15rem;
        margin: 0 auto;
        @include m.semi-tablet {
            margin-bottom: 2rem;
        }
    }
    &__paragraph{
        padding: 1rem;
        font-size: 1.5rem;
        font-weight: 400;
        &--green{
            background-color: v.$green-p;
        }
        &--blue{
            background-color: v.$blue-p;
        }
        @include m.desktop {
            font-size: 2rem;
            padding: 2rem;
        }
    }
    &__span{
        font-weight: 300;
    }

}