@use 'variables' as v;

@mixin phone{
    @media (min-width: v.$phone){
        @content;
    }
}

@mixin max-tablet{
    @media (max-width: v.$tablet){
        @content;
    }
}

@mixin semi-tablet{
    @media (min-width: v.$semi-tablet){
        @content;
    }
}

@mixin tablet{
    @media (min-width: v.$tablet){
        @content;
    }
}

@mixin desktop{
    @media (min-width: v.$desktop){
        @content;
    }
}
@mixin desktopXL{
    @media (min-width: v.$desktopXL){
        @content;
    }
}

// PARA EL GRID LAYOUT
@mixin grid ($col, $gap){
    display: grid;
    grid-template-columns: repeat($col, 1fr);
    gap: $gap;
}