@use '../base/variables' as v;
@use 'base/mixins' as m;

.header {
    background-color: v.$green-ligth;
    &__title {
        text-align: center;
        @include m.tablet;
    }

    &__nav {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__title {
        color: v.$white;
        font-size: 2rem;

        @include m.tablet {
            font-size: 3rem;
        }
    }

    &__link {
        color: v.$white;
        font-size: 2rem;
        padding-bottom: 1rem;
        border-bottom: 1px solid transparent;
        transition: .3s;

        &:hover {
            color: v.$yellow;
            border-bottom: 1px solid v.$white;
        }

        @include m.tablet {
            font-size: 2rem;
            padding: 0;
            border: none;

            &:hover {
                color: v.$purple;
                border: none;
            }
        }

        @include m.desktop {
            font-size: 2.2rem;
        }
    }
}
.nav__principal {
    background-color: transparent;
    padding: 0;
    width: 40%;
    max-width: 40rem;
    height: auto;
    margin: 0;
    @include m.max-tablet {
        transition: .5s linear;
        position: fixed;
        top: 0;
        right: 0;
        transform: translateY(-150%);
        width: 100%;
        max-width: 100%;
        height: 50vh;
        background-color: v.$green;
        padding: 1.5rem 2rem;
        z-index: 10;
    }
    &__container {
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 100%;
        justify-content: space-around;
        position: relative;
    }

    @include m.tablet {
        &__container {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            width: 100%;
        }
    }
}
.nav__principal--active {
    transform: translateY(0);
}
.header__nav--fixed{
    transition: .5s linear;
    position: fixed;
    width: 100%;
    height: auto;
    top: 0;
    left: 0;
    background-color: v.$green-ligth;
    max-width: 100%;
    padding: 0 3rem;
    z-index: 5;
    box-shadow: 0px 1px 3px #000;
}
.menu {
    @include m.tablet {
        display: none;
    }
}
.clouse {
    position: absolute;
    top: 1rem;
    right: 1rem;
}
.body-scroll{
    padding-top: 8rem;
}
.bodyStop{
    overflow: hidden;
}