body {
  margin: 0;
  padding: 0;
  font-size: 16px;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

a {
  transition: all 0.3s;
  text-decoration: none;
  display: inline-block;
}

*,
*:after,
*:before {
  box-sizing: border-box;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
  overflow-x: hidden;
}

p {
  font-size: 2rem;
  color: #0a0a0a;
}

.container {
  width: 95%;
  max-width: 125rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
  transition: 0.3s linear;
}

h2, h3 {
  margin: 0;
  margin-bottom: 3rem;
}

h1 {
  padding: 2rem 0;
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

button {
  cursor: pointer;
}

.section {
  padding: 5rem 0;
}
@media (min-width: 576px) {
  .section {
    padding: 3.5rem 0;
  }
}

.title {
  font-size: 2.5rem;
  padding: 1.5rem 0;
  text-align: center;
}
@media (min-width: 480px) {
  .title {
    font-size: 2.8rem;
  }
}
@media (min-width: 576px) {
  .title {
    font-size: 3.3rem;
    padding-bottom: 0;
  }
}

.subtitle {
  font-size: 1.5rem;
  margin: 0;
}

.text {
  font-size: 1.6rem;
  line-height: 1.7;
  font-weight: 300;
}
@media (min-width: 576px) {
  .text {
    font-size: 1.8rem;
  }
}
@media (min-width: 768px) {
  .text {
    font-size: 2rem;
  }
}
@media (min-width: 992px) {
  .text {
    font-size: 1.5rem;
  }
}
@media (min-width: 1300px) {
  .text {
    font-size: 2rem;
  }
}

.header {
  background-color: #4cb8b3;
}
.header__title {
  text-align: center;
}
.header__nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__title {
  color: #e8e8e8;
  font-size: 2rem;
}
@media (min-width: 768px) {
  .header__title {
    font-size: 3rem;
  }
}
.header__link {
  color: #e8e8e8;
  font-size: 2rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid transparent;
  transition: 0.3s;
}
.header__link:hover {
  color: #fef46d;
  border-bottom: 1px solid #e8e8e8;
}
@media (min-width: 768px) {
  .header__link {
    font-size: 2rem;
    padding: 0;
    border: none;
  }
  .header__link:hover {
    color: #752f97;
    border: none;
  }
}
@media (min-width: 992px) {
  .header__link {
    font-size: 2.2rem;
  }
}

.nav__principal {
  background-color: transparent;
  padding: 0;
  width: 40%;
  max-width: 40rem;
  height: auto;
  margin: 0;
}
@media (max-width: 768px) {
  .nav__principal {
    transition: 0.5s linear;
    position: fixed;
    top: 0;
    right: 0;
    transform: translateY(-150%);
    width: 100%;
    max-width: 100%;
    height: 50vh;
    background-color: #119468;
    padding: 1.5rem 2rem;
    z-index: 10;
  }
}
.nav__principal__container {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  justify-content: space-around;
  position: relative;
}
@media (min-width: 768px) {
  .nav__principal__container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
  }
}

.nav__principal--active {
  transform: translateY(0);
}

.header__nav--fixed {
  transition: 0.5s linear;
  position: fixed;
  width: 100%;
  height: auto;
  top: 0;
  left: 0;
  background-color: #4cb8b3;
  max-width: 100%;
  padding: 0 3rem;
  z-index: 5;
  box-shadow: 0px 1px 3px #000;
}

@media (min-width: 768px) {
  .menu {
    display: none;
  }
}

.clouse {
  position: absolute;
  top: 1rem;
  right: 1rem;
}

.body-scroll {
  padding-top: 8rem;
}

.bodyStop {
  overflow: hidden;
}

.video {
  position: relative;
  max-height: 70vh;
  overflow: hidden;
}
.video__overlay {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  position: absolute;
  background: linear-gradient(to top, rgba(85, 5, 85, 0.607), rgba(0, 0, 0, 0.771));
}
.video__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media (min-width: 576px) {
  .video__container {
    align-items: start;
  }
}
.video__item {
  width: 100%;
  height: 100%;
}
.video__title {
  color: #e8e8e8;
  font-size: 2.5rem;
}
@media (min-width: 576px) {
  .video__title {
    font-size: 3.5rem;
  }
}
@media (min-width: 768px) {
  .video__title {
    font-size: 4rem;
  }
}
@media (min-width: 992px) {
  .video__title {
    font-size: 6rem;
  }
}
.video__paragraph {
  padding: 1.5rem 2rem;
  background-color: #f53756;
  display: inline-block;
  font-size: 1.5rem;
}
@media (min-width: 992px) {
  .video__paragraph {
    font-size: 1.8rem;
  }
}
@media (min-width: 1300px) {
  .video__paragraph {
    font-size: 2.4rem;
  }
}
.video__paragraph {
  color: #e8e8e8;
}

.main {
  width: 100%;
  background-color: #e8e8e8;
}
@media (min-width: 992px) {
  .main__container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
  }
}
.main__subtitle {
  font-weight: 700;
  color: #f53756;
}
@media (min-width: 576px) {
  .main__subtitle {
    margin: 4rem 0 3rem 0;
  }
}
@media (min-width: 992px) {
  .main__subtitle {
    font-size: 2.5rem;
  }
}
.main__text {
  margin-top: 3rem;
}

.line-up {
  background-color: #f53756;
}
.line-up__title {
  color: #e8e8e8;
}
.line-up__subtitle {
  color: #e8e8e8;
  font-size: 1.4rem;
  text-align: center;
  margin-bottom: 0;
  padding: 2rem;
}
.line-up__subtitle--black {
  color: #0a0a0a;
  font-weight: 400;
}
@media (min-width: 576px) {
  .line-up__subtitle {
    padding: 3rem 0;
    font-size: 2rem;
  }
}
.line-up__card-container {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
}
@media (min-width: 768px) {
  .line-up__card-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 10rem;
  }
}
.line-up__card {
  padding-top: 1.5rem;
  background-color: rgb(29, 104, 255);
  box-shadow: 0px 2px 6px #0a0a0a;
}
.line-up__card--green {
  background-color: #4cb8b3;
}
.line-up__img {
  width: 30%;
  max-width: 15rem;
  margin: 0 auto;
}
@media (min-width: 576px) {
  .line-up__img {
    margin-bottom: 2rem;
  }
}
.line-up__paragraph {
  padding: 1rem;
  font-size: 1.5rem;
  font-weight: 400;
}
.line-up__paragraph--green {
  background-color: #43a09b;
}
.line-up__paragraph--blue {
  background-color: rgb(26, 93, 226);
}
@media (min-width: 992px) {
  .line-up__paragraph {
    font-size: 2rem;
    padding: 2rem;
  }
}
.line-up__span {
  font-weight: 300;
}

.galery {
  background-color: #e8e8e8;
  position: relative;
}
.galery__container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
}
@media (min-width: 768px) {
  .galery__container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 4rem;
  }
}
@media (min-width: 992px) {
  .galery__container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 3rem;
  }
}
.galery__img {
  opacity: 0.9;
  transition: 0.3s linear;
  cursor: pointer;
  object-fit: cover;
  height: 20rem;
  width: 100%;
}
.galery__img:hover {
  opacity: 1;
}

.ligthbox {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.8274509804);
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: grid;
  place-content: center;
  opacity: 0;
  z-index: -1;
  transform: scale(1.1);
  transition: 0.3s linear;
}
.ligthbox__img {
  transition: 0.3s linear;
  display: flex;
  margin: 0 auto;
  margin-top: 5rem;
  width: 90%;
  max-width: 50rem;
  overflow: hidden;
  transform: scale(1.1);
}
.ligthbox__clouse {
  display: block;
  margin: 0 auto;
  margin-top: 2rem;
  width: 6rem;
  cursor: pointer;
}
@media (min-width: 576px) {
  .ligthbox__clouse {
    width: 8rem;
  }
}

.ligthbox--active {
  opacity: 1;
  z-index: 5;
}

.boletos {
  background-color: #119468;
  padding-bottom: 7rem;
}
.boletos__container {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
}
@media (min-width: 768px) {
  .boletos__container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 4rem;
  }
}
.boletos__title {
  color: #e8e8e8;
}
.boletos__subtitle {
  margin-bottom: 3rem;
  color: #e8e8e8;
  text-align: center;
}

.card__paragraph {
  color: #e8e8e8;
  text-align: center;
  background-color: #f53756;
}

.card__item {
  background-color: #e8e8e8;
  border-bottom: 1px solid #d0d0d0;
  list-style: none;
}

.card__footer {
  background-color: #752f97;
  text-align: center;
  color: #e8e8e8;
}

.card__item,
.card__paragraph,
.card__footer {
  padding: 1rem;
}

.footer {
  background-color: #f53756;
  padding: 2rem;
}
@media (min-width: 768px) {
  .footer {
    padding: 3rem;
  }
}
.footer__paragraph {
  color: #e8e8e8;
  text-align: center;
  font-size: 1.5rem;
  margin: 0;
}