@use '../base/variables' as v;
@use 'base/mixins' as m;


.video{
    position: relative;
    max-height: 70vh;
    overflow: hidden;
    &__overlay{
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        position: absolute;
        background: linear-gradient(to top, rgba(85, 5, 85, 0.607), rgba(0, 0, 0, 0.771));
    }
    &__container{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        @include m.semi-tablet {
            align-items: start;
        }
    }
    &__item{
        width: 100%;
        height: 100%;
    }
    &__title{
        color: v.$white;
        font-size: 2.5rem;
        @include m.semi-tablet {
            font-size: 3.5rem;
        }
        @include m.tablet {
            font-size: 4rem;
        }
        @include m.desktop {
            font-size: 6rem;
        }

    }
    &__paragraph{
        padding: 1.5rem 2rem;
        background-color: v.$pink;
        display: inline-block;
        font-size: 1.5rem;
        @include m.desktop {
            font-size: 1.8rem;
        }
        @include m.desktopXL {
            font-size: 2.4rem;
        }
    }
    &__paragraph{
        color: v.$white;
    }

}