@use '../base/variables' as v;
@use 'base/mixins' as m;

.main{
    width: 100%;
    background-color: v.$white;
    &__container{
        @include m.desktop {
            @include m.grid(2, 3rem);
        }
    }
    &__subtitle{
        font-weight: 700;
        color: v.$pink;
        @include m.semi-tablet{
            margin: 4rem 0 3rem 0;
        }
        @include m.desktop {
            font-size: 2.5rem;
        }
    }
    &__text{
        margin-top: 3rem;
    }
}