@use '../base/variables' as v;
@use 'base/mixins' as m;

.boletos {
    background-color: v.$green;
    padding-bottom: 7rem;
    &__container {
        @include m.grid(1, 2rem);
        @include m.tablet {
            @include m.grid(2, 4rem);
        }
    }
    &__title{
        color: v.$white;
    }
    &__subtitle {
        margin-bottom: 3rem;
        color: v.$white;
        text-align: center;
    }

}

.card__paragraph {
    color: v.$white;
    text-align: center;
    background-color: v.$pink;
}

.card__item {
    background-color: v.$white;
    border-bottom: 1px solid #d0d0d0;
    list-style: none;
}
.card__footer{
    background-color: v.$purple;
    text-align: center;
    color: v.$white;
}
.card__item,
.card__paragraph,
.card__footer{
    padding: 1rem;
}