@use 'variables' as v;
@use 'base/mixins' as m;
*,
*:after,
*:before{
    box-sizing: border-box;
}
body{
    font-family: v.$font-principal;
    font-size: 1.6rem;
    overflow-x: hidden;
}
p{
    font-size: 2rem;
    color: v.$black;
}
.container{
    width: 95%;
    max-width: 125rem;
    margin: 0 auto;
}
a{
    text-decoration: none;
    transition: .3s linear;
}
h2,h3{
    margin: 0;
    margin-bottom: 3rem;
}
h1{
    padding: 2rem 0;
    font-size: 4rem;
}
h2{
    font-size: 3.6rem;
}
h3{
    font-size: 3rem;
}
button{
    cursor: pointer;
}
.section{
    padding: 5rem 0;
    @include m.semi-tablet {
        padding: 3.5rem 0;
    }
}
.title{
    font-size: 2.5rem;
    padding: 1.5rem 0;
    text-align: center;
    @include m.phone {
        font-size: 2.8rem;
    }
    @include m.semi-tablet {
        font-size: 3.3rem;
        padding-bottom: 0;
    }
}
.subtitle{
    font-size: 1.5rem;
    margin: 0;
}
.text{
    font-size: 1.6rem;
    line-height: 1.7;
    font-weight: 300;
    @include m.semi-tablet {
        font-size: 1.8rem;
    }
    @include m.tablet {
        font-size: 2rem;
    }
    @include m.desktop {
        font-size: 1.5rem;
    }
    @include m.desktopXL {
        font-size: 2rem;
    }
}
