@use '../base/variables' as v;
@use 'base/mixins' as m;

.galery{
    background-color: v.$white;
    position: relative;
    &__container{
        @include m.grid (2, 1rem);
        @include m.tablet {
            @include m.grid(3, 4rem);
        }
        @include m.desktop {
            @include m.grid(4, 3rem);
        }
    }
    &__img{
        opacity: .9;
        transition: .3s linear;
        cursor: pointer;
        object-fit: cover;
        height: 20rem;
        width: 100%;
        &:hover{
            opacity: 1;
        }
    }

}
.ligthbox{
    position: absolute;
    background-color: #000000d3;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    display: grid;
    place-content: center;
    opacity: 0;
    z-index: -1;
    transform: scale(1.1);
    transition: .3s linear;
    &__img{
        transition: .3s linear;
        display: flex;
        margin: 0 auto;
        margin-top: 5rem;
        width: 90%;
        max-width: 50rem;
        overflow: hidden;
        transform: scale(1.1);
    }
    &__clouse{
        display: block;
        margin: 0 auto;
        margin-top: 2rem;
        width: 6rem;
        cursor: pointer;
        @include m.semi-tablet {
            width: 8rem;
        }
    }
}
.ligthbox--active{
    opacity: 1;
    z-index: 5;
}